import { SearchHistoryItem, SearchHistoryType } from '../types/vendor-type';

const STREATS_HISTORY = 'STREATS_HISTORY';
const MAX_HISTORY = 100;

interface HistoryType {
  nextIndex: number;
  items: SearchHistoryType[];
}

const initialHistory: string = `{
  "nextIndex": 1000,
  "items": [
    { "id": 100, "type": "General", "value": "burgers" },
    { "id": 101, "type": "General", "value": "pizza" },
    { "id": 102, "type": "General", "value": "sandwiches" },
    { "id": 103, "type": "General", "value": "barbecue" },
    { "id": 104, "type": "General", "value": "tacos" },
    { "id": 105, "type": "General", "value": "mexican" },
    { "id": 106, "type": "General", "value": "chinese" },
    { "id": 107, "type": "General", "value": "japanese" },
    { "id": 108, "type": "General", "value": "thai" },
    { "id": 109, "type": "General", "value": "italian" },
    { "id": 110, "type": "General", "value": "greek" }
  ]
}`;

if (!window.localStorage.getItem(STREATS_HISTORY)) {
  window.localStorage.setItem(STREATS_HISTORY, initialHistory);
}

export function getAllSearchHistory(): SearchHistoryType[] {
  const text = window.localStorage.getItem(STREATS_HISTORY) ?? initialHistory;

  const history: HistoryType = JSON.parse(text);
  return history.items;
}

export function saveSearchHistoryItem(
  type: SearchHistoryItem,
  value: string
): void {
  const text: string = window.localStorage.getItem(STREATS_HISTORY) ?? '';
  const history: HistoryType = JSON.parse(text);

  let pos = history.items.findIndex(
    (item: SearchHistoryType) => item.value === value
  );

  if (pos < 0) {
    pos = history.items.length;
  }

  for (let i = pos; i > 0; i--) {
    history.items[i] = history.items[i - 1];
  }

  const id = history.nextIndex++;
  history.items[0] = { id, type, value };
  if (history.items.length > MAX_HISTORY) {
    history.items.pop();
  }

  window.localStorage.setItem(STREATS_HISTORY, JSON.stringify(history.items));
}
