/* eslint-disable */
import React from 'react';
import styled from 'styled-components';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGlobeAmericas } from '@fortawesome/free-solid-svg-icons';
import { faEnvelope } from '@fortawesome/free-regular-svg-icons';
import { LinkType, LinkInfoType } from '../types/vendor-type';

export interface WebLinkProps {
  data: LinkInfoType;
}

const SocialIcon = styled.img`
  width: 25px;
  height: 25px;
  margin-left: 5px;
`;

const Icon = styled(FontAwesomeIcon)`
  color: #777;
  font-size: 17pt;
  position: relative;
  top: 4px;
`;

export default function WebLink({
  data,
}: WebLinkProps): React.ReactElement<WebLinkProps> | null {
  let icon = null;
  switch (data.type) {
    case LinkType.Facebook:
      icon = <SocialIcon src="./images/common/facebook.png" />;
      break;
    case LinkType.Twitter:
      icon = <SocialIcon src="./images/common/twitter.png" />;
      break;

    case LinkType.Instagram:
      icon = <SocialIcon src="./images/common/instagram.png" />;
      break;

    case LinkType.Yelp:
      icon = <SocialIcon src="./images/common/yelp.png" />;
      break;

    case LinkType.Www:
      icon = <Icon icon={faGlobeAmericas} />;
      break;

    default:
      return null;
  }

  return (
    <a href={data.url} target="_blank" rel="noopener noreferrer">
      {icon}
    </a>
  );
}
