import {
  DailyScheduleType,
  GeoCoordinatesType,
  ScheduledAppearanceType,
} from '../types/vendor-type';

function promiseCallback(resolve: any, reject: any) {
  if (!window.navigator.geolocation) {
    reject(new Error('Geolocation not supported on this browser.'));
    return;
  }

  window.navigator.geolocation.getCurrentPosition(resolve, reject);
}

export async function getGeoLocation(): Promise<GeoCoordinatesType> {
  const position: any = await new Promise(promiseCallback);

  return position.coords;
}

export enum GeoUnit {
  Kilometers = 'K',
  Miles = 'M',
  Knots = 'K',
}

export function calculateGeoDistance(
  p1: GeoCoordinatesType,
  p2: GeoCoordinatesType,
  units: GeoUnit
) {
  if (p1.latitude === p2.latitude && p1.longitude === p2.longitude) {
    return 0;
  }
  const radlat1 = (Math.PI * p1.latitude) / 180;
  const radlat2 = (Math.PI * p2.latitude) / 180;
  const theta = p1.longitude - p2.longitude;
  const radtheta = (Math.PI * theta) / 180;
  let dist =
    Math.sin(radlat1) * Math.sin(radlat2) +
    Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
  if (dist > 1) {
    dist = 1;
  }
  dist = Math.acos(dist);
  dist = (dist * 180) / Math.PI;
  dist = dist * 60 * 1.1515;
  if (units === GeoUnit.Kilometers) {
    dist *= 1.609344;
  }
  if (units === GeoUnit.Knots) {
    dist *= 0.8684;
  }
  return Math.floor(dist * 100) / 100;
}

export async function sortByDistance(
  schedule: DailyScheduleType[]
): Promise<void> {
  const userLocation = await getGeoLocation();

  schedule.forEach((daySchedule: DailyScheduleType) => {
    daySchedule.appearances.forEach((appearance: ScheduledAppearanceType) => {
      try {
        /* eslint-disable no-param-reassign */
        appearance.distance = calculateGeoDistance(
          appearance.event.geoLocation,
          userLocation,
          GeoUnit.Miles
        );
      } catch (err) {
        console.error('Error calculating distance');
        throw err;
      }
    });

    daySchedule.appearances.sort(
      (a: ScheduledAppearanceType, b: ScheduledAppearanceType) =>
        a.distance && b.distance ? a.distance - b.distance : 1000
    );
  });
}

