import React from 'react';
import styled from 'styled-components';

import { Emoji } from './emoji';

const ChipContainer = styled.span`
  padding: 8px;
  border-radius: 24px;
  background-color: #f5f5f5;
  margin-right: 16px;
`;

export interface ReviewReactionChipProps {
  icon: any;
  count: number;
}

export function ReviewReactionChip({
  icon,
  count,
}: ReviewReactionChipProps): React.ReactElement | null {
  if (!count) {
    return null;
  }

  return (
    <ChipContainer>
      <Emoji ariaLabel="reaction">{icon}</Emoji> {count}
    </ChipContainer>
  );
}
