import React from 'react';
import styled from 'styled-components';
import {
  Timeline,
  TimelineItem,
  TimelineContent,
  TimelineOppositeContent,
  TimelineSeparator,
  TimelineDot,
  TimelineConnector,
} from '@material-ui/lab';
import { Fastfood } from '@material-ui/icons';

import { Paper, Typography } from '@material-ui/core';
import {
  formatDateLongNoYear,
  localTimeShort,
} from '../../services/date-service';
import { useAppSelector } from '../../store';
import { vendorScheduleSelector } from '../../store/selectors';
import { ScheduledAppearanceType } from '../../types/vendor-type';

const Container = styled(Timeline)`
  .card {
    padding: 10px 16px;
  }

  .left {
    flex-grow: 1;
  }

  .right {
    flex-grow: 4;
  }
`;

interface VendorScheduleType {
  vendorId: number;
}

export default function VendorSchedule({
  vendorId,
}: VendorScheduleType): React.ReactElement<VendorScheduleType> {
  const schedule = useAppSelector(vendorScheduleSelector(vendorId));

  return (
    <Container>
      {schedule.map((appearance: ScheduledAppearanceType, index: number) => {
        return (
          <TimelineItem key={index}>
            <TimelineOppositeContent className="left">
              <Typography variant="body2" color="textSecondary">
                <div>{formatDateLongNoYear(appearance.startDateUtc)} </div>
                <div>
                  {localTimeShort(appearance.startDateUtc)} -{' '}
                  {localTimeShort(appearance.endDateUtc)}
                </div>
              </Typography>
            </TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot>
                <Fastfood />
              </TimelineDot>
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent className="right">
              <Paper elevation={3} className="card">
                <div>
                  <strong>{appearance.event.name}</strong>
                  <div>{appearance.event.address.street}</div>
                  {appearance.event.address.street2 && (
                    <div>{appearance.event.address.street2}</div>
                  )}
                  <div>
                    {appearance.event.address.city},{' '}
                    {appearance.event.address.state}{' '}
                    {appearance.event.address.zipCode}
                  </div>
                </div>
              </Paper>
            </TimelineContent>
          </TimelineItem>
        );
      })}
    </Container>
  );
}
