/* eslint-disable */
const dayOfWeek = [
  'Sunday',
  'Monday',
  'Tuesday',
  'Wednesday',
  'Thursday',
  'Friday',
  'Saturday',
];

const months = [
  'January',
  'February',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

// Returns: 2021/06/22
export function formatDateYyyyMmDd(date?: Date): string {
  if (!date) {
    return '';
  }

  const month = (date.getMonth() + 1).toString().padStart(2, '0');
  const day = date.getDate().toString().padStart(2, '0');
  return `${date.getFullYear()}/${month}/${day}`;
}

// Returns: Tuesday, June 22, 2021
export function formatDateLong(dateStr?: string): string {
  if (!dateStr) {
    return '';
  }

  const date = new Date(dateStr);
  // prettier-ignore
  return `${dayOfWeek[date.getDay()]}, ${months[date.getMonth()]} ${date.getDate()}, ${date.getFullYear()}`;
}

export function formatDateLongNoYear(dateStr?: string): string {
  if (!dateStr) {
    return '';
  }

  const date = new Date(dateStr);
  // prettier-ignore
  return `${dayOfWeek[date.getDay()]}, ${months[date.getMonth()]} ${date.getDate()}`;
}

// Returns: June 22
export function formatDateShort(dateStr?: string): string {
  if (!dateStr) {
    return '';
  }

  const date = new Date(dateStr);
  // prettier-ignore
  return `${months[date.getMonth()]} ${date.getDate()}`;
}

// Returns: today, tomorrow, or name of weekday
export function getDayName(dayNumber: number): string {
  if (dayNumber === 0) {
    return 'today';
  }

  if (dayNumber === 1) {
    return 'tomorrow';
  }

  const date = new Date();
  date.setDate(date.getDate() + dayNumber);

  return dayOfWeek[date.getDay()];
}

// Returns 8:45am, 7:30pm
export function localTimeShort(utcTime: string) {
  const d = new Date(utcTime);
  let hour = d.getHours();
  let ampm = 'am';
  if (hour >= 12) {
    hour -= 12;
    if (hour === 0) {
      hour = 12;
    }
    ampm = 'pm';
  }
  let minute = d.getMinutes().toString();
  if (minute.length < 2) {
    minute = `0${minute}`;
  }

  return `${hour}:${minute}${ampm}`;
}
