import React from 'react';

export interface EmojiProps {
  ariaLabel: string;
  children: React.ReactChild;
}

export function Emoji({
  ariaLabel,
  children,
  ...others
}: EmojiProps): React.ReactElement<EmojiProps> {
  return (
    <span
      role="img"
      aria-label={ariaLabel || ''}
      aria-hidden={ariaLabel ? 'false' : 'true'}
      {...others}
    >
      {children}
    </span>
  );
}
