import React from 'react';

import { AppStateType, DailyScheduleType } from '../../types/vendor-type';

import AppHeader from './app-header';
import { PageLoader } from '../common';
import DailySchedule from './daily-schedule';

export default function DailyScheduleFeed(): React.ReactElement {
  return (
    <div>
      <AppHeader />
      <PageLoader
        renderOnReady={({ schedule }: AppStateType) =>
          schedule.map((daySchedule: DailyScheduleType) => (
            <DailySchedule key={daySchedule.dayNumber} schedule={daySchedule} />
          ))
        }
      />
    </div>
  );
}
