export function stateAbbreviation(stateName: string): string | null {
  if (!stateName) {
    return null;
  }

  if (statesByAbbreviation[stateName.toUpperCase()]) {
    return stateName;
  }

  const abbreviation = statesByName[stateName.toLowerCase()];
  if (abbreviation) {
    return abbreviation;
  }

  return null;
}

export function fiveDigitZipCode(zipCode: string): string | null {
  const num = parseInt(zipCode?.substring(0, 5));
  return isNaN(num) ? null : num.toString();
}
