import React from 'react';
import styled from 'styled-components';

import media, { sizes } from '../../media';

import { useAppSelector } from '../../store';
import { LoadState } from '../../store/app-state-slice';
import {
  appStateSliceSelector,
  loadStateSelector,
} from '../../store/selectors';
import { AppStateType } from '../../types/vendor-type';

export const PageLoaderBody = styled.div.attrs({ id: 'page-loader' })`
  @media screen and ${media.tablet} {
    width: ${sizes.medium};
    margin: auto;
  }
`;

interface PageLoaderProps {
  renderOnReady: (appState: AppStateType) => any;
  renderOnLoading?: () => any;
}

function DefaultLoader() {
  return <div>Loading...</div>;
}

export function PageLoader({
  renderOnReady,
  renderOnLoading = DefaultLoader,
}: PageLoaderProps): React.ReactElement<PageLoaderProps> {
  const loadState: LoadState = useAppSelector(loadStateSelector);
  const appState = useAppSelector(appStateSliceSelector);

  let content = [];
  switch (loadState) {
    case LoadState.Loading:
      content = renderOnLoading();
      break;

    case LoadState.Loaded:
      content = renderOnReady(appState);
      break;
  }

  return <PageLoaderBody>{content}</PageLoaderBody>;
}
