import React, { useEffect } from 'react';
import { Switch, Route } from 'react-router-dom';
import { useDispatch } from 'react-redux';

import DailyScheduleFeed from './components/daily-schedule-feed/daily-schedule-feed';
import VendorDetails from './components/vendor-details-page/vendor-details';
import SearchResults from './components/search-results/search-results';

import NoMatch from './components/NoMatch';
import { LoadState } from './store/app-state-slice';
import { loadStateSelector, useAppSelector } from './store';
import { loadAppStateThunk } from './store/load-app-state-thunk';

import TestSticky from './test-sticky';

function App(): React.ReactElement {
  const dispatch = useDispatch();
  const loadState: LoadState = useAppSelector(loadStateSelector);

  useEffect(() => {
    if (loadState === LoadState.NotLoaded) {
      dispatch(loadAppStateThunk());
    }
  }, [loadState]);

  return (
    <div>
      <Switch>
        <Route exact path="/" component={DailyScheduleFeed} />
        <Route path="/vendors/:vendorId" component={VendorDetails} />
        <Route path="/search" component={SearchResults} />
        <Route path="/test-sticky" component={TestSticky} />
        <Route component={NoMatch} />
      </Switch>
    </div>
  );
}

export default App;
