import React from 'react';
import { Star, StarHalf, StarOutline } from '@material-ui/icons';

export interface StarRatingProps {
  stars: number;
}

export function StarRating({
  stars,
}: StarRatingProps): React.ReactElement<StarRatingProps> {
  const value = stars || 0;

  let filledStars = Math.floor(value);
  let fraction = value - Math.floor(value);
  if (fraction <= 0.1) {
    fraction = 0;
  } else if (fraction >= 0.9) {
    fraction = 0;
    filledStars += 1;
  } else {
    fraction = 1;
  }

  const iconList = [];
  for (let i = 0; i < 5; i++) {
    if (i < filledStars) {
      iconList.push(<Star key={i} />);
    } else if (i < filledStars + fraction) {
      iconList.push(<StarHalf key={i} />);
    } else {
      iconList.push(<StarOutline key={i} />);
    }
  }
  return <span>{iconList}</span>;
}
