import { createSelector } from '@reduxjs/toolkit';

import { RootState } from '../store';
import {
  DailyScheduleType,
  ScheduledAppearanceType,
} from '../types/vendor-type';
import { AppStateSliceType, LoadState } from './app-state-slice';

export const appStateSliceSelector = (state: RootState) => state.appState;

export const loadStateSelector = createSelector(
  appStateSliceSelector,
  (appState: AppStateSliceType) => appState.loadState
);

export const isLoadingSelector = createSelector(
  appStateSliceSelector,
  (appState: AppStateSliceType) => appState.loadState === LoadState.Loading
);

export const allVendorsSelector = createSelector(
  appStateSliceSelector,
  (appState: AppStateSliceType) => appState.vendors
);

export const vendorSelector = (vendorId: number) =>
  createSelector(
    appStateSliceSelector,
    (appState: AppStateSliceType) => appState.vendors[vendorId]
  );

export const allEventsSelector = createSelector(
  appStateSliceSelector,
  (appState: AppStateSliceType) => appState.events
);

export const allScheduledSelector = createSelector(
  appStateSliceSelector,
  (appState: AppStateSliceType) => appState.schedule
);

export const vendorScheduleSelector = (vendorId: number) =>
  createSelector(allScheduledSelector, (schedule: DailyScheduleType[]) =>
    schedule
      .map((day: DailyScheduleType) =>
        day.appearances.filter(
          (app: ScheduledAppearanceType) => app.vendor.id === vendorId
        )
      )
      .flat()
  );

export const searchHistorySelector = createSelector(
  appStateSliceSelector,
  (appState: AppStateSliceType) => appState.searchHistory
);

export const loadErrorsSelector = createSelector(
  appStateSliceSelector,
  (appState: AppStateSliceType) => appState.errors
);
