import { rrulestr } from 'rrule';
import {
  EventType,
  DailyScheduleType,
  ScheduledAppearanceType,
  VendorsAndEventsType,
} from '../types/vendor-type';
import { formatDateYyyyMmDd } from './date-service';

interface DateMapType {
  [key: string]: ScheduledAppearanceType[];
}

export function getFiveDaySchedule({
  vendors,
  events,
}: VendorsAndEventsType): DailyScheduleType[] {
  if (!events || !vendors) {
    throw Error("Can't have null events or null vendors!");
  }

  const startDate = new Date();
  const endDate = new Date();
  endDate.setDate(startDate.getDate() + 4);

  const dateMap: DateMapType = {};

  Object.keys(events).forEach((eventId: string) => {
    const event: EventType = events[+eventId];
    const rruleSet = rrulestr(event.recurrences, { forceset: true });
    const upcomingDates = rruleSet.between(startDate, endDate);
    upcomingDates.forEach((utcDate: Date): void => {
      const dateHash: string = formatDateYyyyMmDd(utcDate);
      if (!(dateHash in dateMap)) {
        dateMap[dateHash] = [];
      }
      const shortDate: string = utcDate.toISOString().substring(0, 10);
      event.vendors.forEach((vendorId: number) => {
        const vendor = vendors[vendorId];
        const appearance: ScheduledAppearanceType = {
          vendor,
          event,
          distance: null,
          startDateUtc: `${shortDate}T${event.startTimeUtc}Z`,
          endDateUtc: `${shortDate}T${event.endTimeUtc}Z`,
        };
        dateMap[dateHash].push(appearance);
      });
    });
  });

  const day = new Date();
  const schedule: DailyScheduleType[] = [];
  const totalDays = 5;

  // prettier-ignore
  for (let dayNumber = 0; dayNumber < totalDays; dayNumber++, day.setDate(day.getDate() + 1)) {
    const key = formatDateYyyyMmDd(day);

    if (!dateMap[key]) {
      continue;
    }

    const appearances: ScheduledAppearanceType[] = dateMap[key];
    delete dateMap[key];

    schedule.push({
      dayNumber,
      date: key,
      appearances,
    });
  }

  if (Object.keys(dateMap).length) {
    console.error('Unprocessed events remaining', dateMap);
  }

  return schedule;
}
