import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Typography } from '@material-ui/core';

import { AppStateType } from '../../types/vendor-type';

import {
  flexsearchLookup,
  FLEXSEARCH_INDEX_EVENTS,
  FLEXSEARCH_INDEX_VENDORS,
} from '../../services/flexsearch-service';
import { Emoji, PageLoader } from '../common';
import AppHeader from '../daily-schedule-feed/app-header';

const Container = styled.div`
  margin-top: 35px;

  .search-query {
    margin-bottom: 32px;
  }
`;

export default function DailyScheduleFeed({
  location,
}: any): React.ReactElement {
  const queryString: string = String(location.search);
  const query: string = queryString.startsWith('?query=')
    ? decodeURIComponent(queryString.substring(7)).replace('+', ' ')
    : '';
  const [searchResults, setSearchResults] = useState<number[]>([]);

  useEffect(() => {
    const results = flexsearchLookup(query);
    setSearchResults(results);
  }, [query]);

  return (
    <div>
      <AppHeader searchQuery={query} />
      <PageLoader
        renderOnReady={(state: AppStateType) => {
          const { vendors, events } = state;

          if (query === '') {
            return (
              <Container>
                <Typography variant="h6">We&apos;re so sorry</Typography>
                <Typography variant="h4">¯\_(ツ)_/¯</Typography>
                <Typography>
                  We can&apos;t perform the search you requested.
                </Typography>
              </Container>
            );
          }

          return (
            <Container>
              <Typography className="search-query" variant="h6">
                Showing results for: &quot;{query}&quot;
              </Typography>
              {!searchResults.length && (
                <Typography>
                  <Emoji ariaLabel="nervous">😅</Emoji> We&apos;re sorry, we
                  weren&apos;t able to find any results for that.
                </Typography>
              )}
              {searchResults.length &&
                searchResults.map((searchKey: number) => {
                  if (searchKey >= FLEXSEARCH_INDEX_EVENTS) {
                    const eventId = searchKey - FLEXSEARCH_INDEX_EVENTS;
                    const event = events[eventId];

                    return <div key={searchKey}>Event: {event.name}</div>;
                  }
                  if (searchKey >= FLEXSEARCH_INDEX_VENDORS) {
                    const vendorId = searchKey - FLEXSEARCH_INDEX_VENDORS;
                    const vendor = vendors[vendorId];

                    return <div key={searchKey}>Vendor: {vendor.name}</div>;
                  }

                  console.warn(
                    'Unknown key found in flex-search results: ',
                    searchKey
                  );
                  return null;
                })}
            </Container>
          );
        }}
      />
    </div>
  );
}
