import React from 'react';
import styled from 'styled-components';

import VendorCard from './vendor-card';
import {
  DailyScheduleType,
  ScheduledAppearanceType,
} from '../../types/vendor-type';

import { getDayName, formatDateLong } from '../../services/date-service';

interface VendorListProps {
  schedule: DailyScheduleType;
}

const Container = styled.div`
  margin-top: 35px;
`;

const Header = styled.div`
  padding: 0 15px;
`;

const SectionHeader = styled.h1`
  font-size: 18pt;
  font-weight: bold;
  margin: 0;
`;

export default function DailySchedule({
  schedule,
}: VendorListProps): React.ReactElement {
  return (
    <Container>
      <Header>
        <SectionHeader>Near you {getDayName(schedule.dayNumber)}</SectionHeader>
        <div>{formatDateLong(schedule.date)}</div>
      </Header>
      {schedule.appearances.map(
        (appearance: ScheduledAppearanceType, i: number) => (
          <VendorCard key={i} data={appearance} />
        )
      )}
    </Container>
  );
}
