/*******************************************************************************
 * Shared Types
 */

export enum ServicePlan {
  None = 'None',
  Basic = 'Basic',
  Pro = 'Pro',
}

export interface AddressType {
  street: string | null;
  street2: string | null;
  city: string | null;
  state: string | null;
  zipCode: string | null;
}

export interface PhotoType {
  url: string;
  caption: string | null;
  badge: string | null;
  isProfilePhoto?: boolean;
  isMenu?: boolean;
  aspectRatio: number[];
}

/*******************************************************************************
 * Vendor Types
 */

export interface RatingType {
  stars: number;
  reviewCount: number;
  percentages: number[];
}

export enum LinkType {
  Facebook = 'facebook',
  Twitter = 'twitter',
  Instagram = 'instagram',
  Yelp = 'yelp',
  Www = 'www',
}

export interface LinkInfoType {
  type: LinkType;
  url: string;
}

export interface VendorType {
  id: number;
  linkKey: number | null;
  name: string;
  tags: string[];
  slogan: string | null;
  about: string | null;
  logoUrl: string | null;
  servicePlan: ServicePlan;
  photos: PhotoType[];
  ratingInfo: RatingType;
  isFavorite: boolean;
  links: LinkInfoType[];
  address: AddressType | null;
  phoneNumber: string | null;
  email: string | null;
  website: string | null;
}

export interface VendorMapType {
  [key: number]: VendorType;
}

/*******************************************************************************
 * Search History Types
 */

export enum SearchHistoryItem {
  General = 'General',
  Vendor = 'Vendor',
  Event = 'Event',
}

export interface SearchHistoryType {
  id: number;
  type: SearchHistoryItem;
  value: string;
}

/*******************************************************************************
 * Event Types
 */

export interface GeoCoordinatesType {
  latitude: number;
  longitude: number;
}

export interface EventType {
  id: number;
  name: string;
  description: string;
  address: AddressType;
  geoLocation: GeoCoordinatesType;
  recurrences: string;
  startDate: string;
  endDate: string;
  startTimeUtc: string;
  endTimeUtc: string;
  vendors: number[];
}

export interface EventMapType {
  [key: number]: EventType;
}

export interface ScheduledAppearanceType {
  vendor: VendorType;
  event: EventType;
  distance: number | null;
  startDateUtc: string;
  endDateUtc: string;
}

export interface DailyScheduleType {
  dayNumber: number;
  date: string;
  appearances: ScheduledAppearanceType[];
}

export interface VendorsAndEventsType {
  vendors: VendorMapType;
  events: EventMapType;
}

export interface AppStateType extends VendorsAndEventsType {
  schedule: DailyScheduleType[];
  searchHistory: SearchHistoryType[];
}
