import {
  VendorType,
  EventType,
  SearchHistoryType,
  AppStateType,
} from '../types/vendor-type';

export const FLEXSEARCH_INDEX_VENDORS = 1000000;
export const FLEXSEARCH_INDEX_EVENTS = 2000000;
export const MAX_RESULTS = 10;

const { FlexSearch }: any = window;

const index = new FlexSearch({
  encode: 'icase',
  tokenize: 'reverse',
  suggest: true,
  cache: true,
});

function indexVendor(vendor: VendorType): void {
  const items = [vendor.name, ...vendor.tags, vendor.about ?? ''];
  const key = items.join(' ');

  index.add(vendor.id + FLEXSEARCH_INDEX_VENDORS, key);
}

function indexEvent(event: EventType): void {
  index.add(event.id + FLEXSEARCH_INDEX_EVENTS, event.name);
}

function indexHistoricalSearchItem(searchItem: SearchHistoryType): void {
  index.add(searchItem.id, searchItem.value);
}

export function buildIndex({
  vendors,
  events,
  searchHistory,
}: AppStateType): void {
  if (searchHistory) {
    searchHistory.forEach((searchItem: SearchHistoryType) => {
      indexHistoricalSearchItem(searchItem);
    });
  }

  if (vendors) {
    Object.keys(vendors).forEach((vendorId: string) => {
      indexVendor(vendors[+vendorId]);
    });
  }

  if (events) {
    Object.keys(events).forEach((eventId: string) => {
      indexEvent(events[+eventId]);
    });
  }
}

export function flexsearchLookup(value: string): number[] {
  return index.search(value, 200);
}
