import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faShareAlt, faHeart, faMap } from '@fortawesome/free-solid-svg-icons';
import { faHeart as faHeartOutline } from '@fortawesome/free-regular-svg-icons';

import media from '../../media';
import { localTimeShort } from '../../services/date-service';
import { ScheduledAppearanceType, LinkInfoType } from '../../types/vendor-type';
import Album from './album';
import { RatingsAndReviews } from '../common';
import WebLink from '../web-link';

export interface VendorCardProps {
  data: ScheduledAppearanceType;
}

const Container = styled.div`
  background-color: white;
  margin: 15px 0px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);

  @media screen and ${media.tablet} {
    border-radius: 10px;
  }
`;

const Section = styled.div`
  padding: 15px;
  font-size: 10pt;
  color: #777;
`;

const Logo = styled.div`
  width: 46px;
  height: 46px;
  float: left;
  margin-right: 10px;
`;

const LogoImg = styled.img`
  width: 100%;
  height: auto;
  display: block;
`;

const VendorName = styled.h1`
  font-size: 18pt;
  margin: 0;
  color: black;
`;

const VendorSlogan = styled.div`
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
`;

const Location = styled.div`
  float: right;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const BottomRow = styled(Section)`
  padding: 0 15px 15px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 20px;

  & > * {
    display: block;
  }
`;

const VendorLink = styled(Link)`
  display: block;
  padding: 15px;
  font-size: 10pt;
  color: #777;
  text-decoration: none;

  &:hover {
    text-decoration: none;
    color: #777;
  }
`;

const LinkSeries = styled.span``;

const ActionIcon = styled(FontAwesomeIcon)`
  font-size: 16pt;
  margin-top: 2px;
`;

export default function VendorCard({
  data,
}: VendorCardProps): React.ReactElement<VendorCardProps> {
  const detailsLink = `vendors/${data.vendor.id}`;

  return (
    <Container>
      <VendorLink to={detailsLink}>
        <Logo>
          <LogoImg src={data.vendor.logoUrl ?? './images/common/no-logo.png'} />
        </Logo>
        <div>
          <VendorName>{data.vendor.name}</VendorName>
          <VendorSlogan>
            {data.vendor.slogan ? data.vendor.slogan : <span>&nbsp;</span>}
          </VendorSlogan>
        </div>
      </VendorLink>
      <Album
        servicePlan={data.vendor.servicePlan}
        photos={data.vendor.photos}
      />
      <Section>
        {data.distance && (
          <Location>
            <div>{data.distance} mi</div>
            <ActionIcon icon={faMap} />
          </Location>
        )}
        {data.event.name && (
          <div>
            <strong>{data.event.name}</strong>
          </div>
        )}
        <div>{data.event.address.street}</div>
        {data.event.address.street && (
          <div>
            {data.event.address.city}, {data.event.address.state}{' '}
            {data.event.address.zipCode}
          </div>
        )}
        <div>
          {localTimeShort(data.startDateUtc)} -{' '}
          {localTimeShort(data.endDateUtc)}
        </div>
      </Section>
      <BottomRow>
        <RatingsAndReviews ratingInfo={data.vendor.ratingInfo} />
        <LinkSeries>
          {data.vendor.links.map((link: LinkInfoType, i: number) => (
            <WebLink key={i} data={link} />
          ))}
        </LinkSeries>
        <LinkSeries>
          <ActionIcon icon={faShareAlt} />
        </LinkSeries>
        <LinkSeries>
          {data.vendor.isFavorite ? (
            <ActionIcon icon={faHeart} />
          ) : (
            <ActionIcon icon={faHeartOutline} />
          )}
        </LinkSeries>
      </BottomRow>
    </Container>
  );
}
