import React from 'react';
import styled from 'styled-components';
import { AccountCircle, Favorite } from '@material-ui/icons';

import { Typography, Fab } from '@material-ui/core';
import { ReviewArticleType } from './vendor-reviews';
import { ReviewReactionChip, StarRating } from '../common';
import { formatDateLong } from '../../services/date-service';

export interface ReviewArticleProps {
  article: ReviewArticleType;
}

const Container = styled.div.attrs({ 'data-id': 'review-article' })`
  padding: 32px;
  border-top: 2px solid #f5f5f5;

  .no-avatar {
    font-size: 3.7rem;
    color: #bbbbbb;
  }

  .light-text {
    color: #999;
    position: relative;
    bottom: 5px;
    display: inline-block;
    margin-left: 16px;
  }
`;

const UserGrid = styled.div`
  display: flex;
  flex-direction: row;
  margin-bottom: 16px;
`;

const AvatarContainer = styled.div`
  margin-right: 16px;
`;

const Paragraph = styled.div`
  margin: 0.5rem 0 0.5rem 0;
`;

const Reactions = styled.div`
  margin-top: 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

export default function ReviewArticle({
  article,
}: ReviewArticleProps): React.ReactElement<ReviewArticleProps> {
  return (
    <Container>
      <UserGrid>
        <AvatarContainer>
          <AccountCircle className="no-avatar" />
        </AvatarContainer>
        <div>
          <Typography variant="h5">{article.name}</Typography>
          <div>
            <StarRating stars={article.starRating} />{' '}
            <span className="light-text">{formatDateLong(article.date)}</span>
          </div>
        </div>
      </UserGrid>

      <Typography variant="h6">{article.title}</Typography>
      <Typography>
        {article.content.map((paragraph: string) => (
          <Paragraph>{paragraph}</Paragraph>
        ))}
      </Typography>
      <Reactions>
        <div>
          <ReviewReactionChip icon="👍" count={article.reactions.thumbsUp} />
          <ReviewReactionChip icon="👎" count={article.reactions.thumbsDown} />
          <ReviewReactionChip icon="❤️" count={article.reactions.loveIt} />
          <ReviewReactionChip icon="😂" count={article.reactions.funny} />
          <ReviewReactionChip icon="😲" count={article.reactions.wow} />
          <ReviewReactionChip icon="😢" count={article.reactions.sad} />
          <ReviewReactionChip icon="😠" count={article.reactions.mad} />
          <ReviewReactionChip icon="🎺" count={article.reactions.reported} />
        </div>
        <Fab size="small" aria-label="like" color="primary">
          <Favorite />
        </Fab>
      </Reactions>
    </Container>
  );
}
