import React from 'react';
import styled from 'styled-components';
import { RatingType } from '../../types/vendor-type';

import { StarRating } from './star-rating';

export interface RatingsAndReviewsProps {
  ratingInfo: RatingType;
  className?: string;
}

const Stats = styled.span`
  display: inline-block;
  margin-left: 8px;
  position: relative;
  bottom: 5px;
`;

export function RatingsAndReviews({
  ratingInfo,
  ...others
}: RatingsAndReviewsProps): React.ReactElement {
  return (
    <span {...others}>
      <StarRating stars={ratingInfo.stars} />
      <Stats>
        {ratingInfo.stars.toFixed(1)} -{' '}
        {ratingInfo.reviewCount.toLocaleString()}
      </Stats>
    </span>
  );
}
