/* eslint-disable no-param-reassign */
import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import {
  AppStateType,
  DailyScheduleType,
  EventMapType,
  SearchHistoryType,
  VendorMapType,
} from '../types/vendor-type';

export enum LoadState {
  NotLoaded = 'not-loaded',
  Loading = 'loading',
  Loaded = 'loaded',
}

export const APPSTATE_SLICE = 'appState';

export interface AppStateSliceType extends AppStateType {
  loadState: LoadState;
  errors: string[];
}

export const initialState: AppStateSliceType = {
  loadState: LoadState.NotLoaded,
  vendors: {},
  events: {},
  schedule: [],
  searchHistory: [],
  errors: [],
};

export const appStateSlice = createSlice({
  name: APPSTATE_SLICE,
  initialState,
  reducers: {
    loadStateBegin: (state) => {
      state.loadState = LoadState.Loading;
    },
    loadStateEnd: (state) => {
      state.loadState = LoadState.Loaded;
    },
    loadError: (state, action: PayloadAction<string>) => {
      state.errors.push(action.payload);
    },
    vendorsLoaded: (state, action: PayloadAction<VendorMapType>) => {
      state.vendors = action.payload;
    },
    eventsLoaded: (state, action: PayloadAction<EventMapType>) => {
      state.events = action.payload;
    },
    scheduleLoaded: (state, action: PayloadAction<DailyScheduleType[]>) => {
      state.schedule = action.payload;
    },
    // prettier-ignore
    searchHistoryUpdated: (state, action: PayloadAction<SearchHistoryType[]>) => {
      state.searchHistory = action.payload;
    },
  },
});

export const {
  loadStateBegin,
  loadStateEnd,
  loadError,
  vendorsLoaded,
  eventsLoaded,
  scheduleLoaded,
  searchHistoryUpdated,
} = appStateSlice.actions;

export default appStateSlice.reducer;
