import { Dispatch } from '@reduxjs/toolkit';
import { getVendorsAndEvents } from '../api/get-vendors-and-events-api';
import { buildIndex } from '../services/flexsearch-service';
import { sortByDistance } from '../services/geolocation-service';
import { getFiveDaySchedule } from '../services/schedule-sercive';
import { getAllSearchHistory } from '../services/search-history-service';
import {
  DailyScheduleType,
  SearchHistoryType,
  VendorsAndEventsType,
} from '../types/vendor-type';
import {
  eventsLoaded,
  loadError,
  loadStateBegin,
  loadStateEnd,
  scheduleLoaded,
  searchHistoryUpdated,
  vendorsLoaded,
} from './app-state-slice';

export function loadAppStateThunk() {
  return async (dispatch: Dispatch) => {
    // , getState: () => RootState
    dispatch(loadStateBegin());

    function handleError(err: Error, message: string) {
      dispatch(loadError(message));
      console.error(message, err);
    }

    let vendorsAndEvents: VendorsAndEventsType = {
      vendors: [],
      events: [],
    };

    try {
      vendorsAndEvents = await getVendorsAndEvents();
    } catch (err) {
      handleError(err, 'Error loading vendors and events');
    }

    let schedule: DailyScheduleType[] = [];
    if (vendorsAndEvents) {
      try {
        schedule = getFiveDaySchedule(vendorsAndEvents);
      } catch (err) {
        handleError(err, 'Error computing five-day schedule');
      }
    }

    if (schedule) {
      try {
        await sortByDistance(schedule);
      } catch (err) {
        handleError(err, 'Error computing geo-location info');
      }
    }

    let searchHistory: SearchHistoryType[] = [];
    try {
      searchHistory = getAllSearchHistory();
    } catch (err) {
      handleError(err, 'Error retrieving search history info');
    }

    try {
      const state = {
        ...vendorsAndEvents,
        schedule,
        searchHistory,
      };

      buildIndex(state);
    } catch (err) {
      handleError(err, 'Error building search index');
    }

    dispatch(vendorsLoaded(vendorsAndEvents.vendors));
    dispatch(eventsLoaded(vendorsAndEvents.events));
    dispatch(scheduleLoaded(schedule));
    dispatch(searchHistoryUpdated(searchHistory));
    dispatch(loadStateEnd());
  };
}
