import axios from 'axios';
import {
  fiveDigitZipCode,
  stateAbbreviation,
} from '../services/address-service';
import {
  VendorType,
  VendorMapType,
  EventType,
  ServicePlan,
  EventMapType,
  VendorsAndEventsType,
} from '../types/vendor-type';

interface FtlTruckType {
  id: number;
  category_name: string | null;
  name: string;
  slug: string;
  city: string | null;
  state: string | null;
  description: string | null;
  about: string | null;
  logo: string | null;
  banner_image: string | null;
}

interface FtlAttendingTruckType {
  id: number;
}

interface FtlGeoLocationType {
  type: string;
  coordinates: number[];
}

interface FtlEventType {
  id: number;
  attending_trucks: FtlAttendingTruckType[];
  occurrences: string[];
  name: string;
  description: string;
  original_name: string;
  start_date: string;
  start: string;
  end: string;
  recurrences: string;
  address: string;
  address2: string | null;
  city: string;
  state: string;
  zip_code: string;
  geo_location: FtlGeoLocationType;
  latitude_offset: string;
  longitude_offset: string;
  first_date: string;
  last_date: string;
}

const firstLetterRegex = /^[A-Za-z]/;
const skipWords = ['*|', '-', 'ftl', 'ftl*', '(ftl)', 'league', 'ln', '@'];
const shortWords = ['a', 'an', 'the', 'of', 'but', 'and', 'or'];

function cleanEventName(name: string): string {
  const sanitized = name
    .split(' ')
    .filter(
      (word: string) => word && skipWords.indexOf(word.toLocaleLowerCase()) < 0
    )
    .map((word: string, index: number) => {
      const match = word.match(firstLetterRegex);
      if (!match) {
        return word;
      }

      const lower = word.toLocaleLowerCase();
      if (index > 0 && shortWords.indexOf(lower) >= 0) {
        return lower;
      }

      if (word.length <= 3) {
        return word;
      }

      return lower.slice(0, 1).toLocaleUpperCase() + lower.slice(1);
    })
    .join(' ');

  return sanitized;
}

function strToNum(str: string): number {
  const num = parseFloat(str);
  return isNaN(num) ? 0 : num;
}

function randomPaddedInt(max: number): string {
  return Math.floor(Math.random() * max + 1)
    .toString()
    .padStart(4, '0');
}

function getTagInfo(ftlTruck: FtlTruckType): string[] {
  // prettier-ignore
  return ftlTruck.category_name
    ?.split("/")
    .map(tag => tag.toLowerCase().trim()) ?? [];
}

function getVendorMap(truckList: FtlTruckType[]): VendorMapType {
  const vendorMap: VendorMapType = {};

  truckList.forEach((ftlTruck: FtlTruckType) => {
    const newVendor: VendorType = {
      id: ftlTruck.id,
      linkKey: null,
      name: ftlTruck.name,
      tags: getTagInfo(ftlTruck),
      slogan: ftlTruck.description,
      about: ftlTruck.about,
      logoUrl: ftlTruck.logo,
      servicePlan: ServicePlan.None,
      photos: [],
      ratingInfo: {
        stars: 0,
        reviewCount: 0,
        percentages: [0, 0, 0, 0, 0],
      },
      isFavorite: false,
      links: [],
      address: null,
      phoneNumber: null,
      email: null,
      website: null,
    };

    if (ftlTruck.banner_image) {
      newVendor.photos.push({
        url: ftlTruck.banner_image,
        caption: null,
        badge: null,
        isProfilePhoto: true,
        aspectRatio: [],
      });
    }

    while (newVendor.photos.length < 4) {
      newVendor.photos.push({
        url: `./images/customers/${randomPaddedInt(4)}/${randomPaddedInt(
          4
        )}.jpg`,
        caption: null,
        badge: null,
        aspectRatio: [1, 1],
      });
    }

    vendorMap[ftlTruck.id] = newVendor;
  });

  return vendorMap;
}

function mergeVendorData(vendorMap: VendorMapType, vendorData: VendorType[]) {
  if (!vendorData) {
    console.warn('Vendor info not loaded');
  }

  vendorData.forEach((vendor: VendorType) => {
    /* eslint-disable no-param-reassign */
    vendorMap[vendor.id] = {
      ...vendorMap[vendor.id],
      ...vendor,
    };
  });
}

function getEventMap(
  vendorMap: VendorMapType,
  eventList: FtlEventType[]
): EventMapType {
  const eventMap: EventMapType = {};

  eventList.forEach((ftlEvent: FtlEventType) => {
    if (!ftlEvent.state || !ftlEvent.address) {
      // Reject events with incomplete address info
      return;
    }

    const newEvent: EventType = {
      id: ftlEvent.id,
      name: cleanEventName(ftlEvent.original_name),
      description: ftlEvent.description,
      geoLocation: {
        latitude:
          ftlEvent.geo_location.coordinates[1] +
          strToNum(ftlEvent.latitude_offset),
        longitude:
          ftlEvent.geo_location.coordinates[0] +
          strToNum(ftlEvent.longitude_offset),
      },
      address: {
        street: ftlEvent.address,
        street2: ftlEvent.address2,
        city: ftlEvent.city,
        state: stateAbbreviation(ftlEvent.state),
        zipCode: fiveDigitZipCode(ftlEvent.zip_code),
      },
      startDate: ftlEvent.first_date,
      endDate: ftlEvent.last_date,
      startTimeUtc: ftlEvent.start,
      endTimeUtc: ftlEvent.end,
      recurrences: ftlEvent.recurrences,
      vendors: [],
    };

    ftlEvent.attending_trucks.forEach((ftlTruck: FtlAttendingTruckType) => {
      if (vendorMap[ftlTruck.id]) {
        newEvent.vendors.push(ftlTruck.id);
      }
    });

    eventMap[ftlEvent.id] = newEvent;
  });

  return eventMap;
}

export async function getVendorsAndEvents(): Promise<VendorsAndEventsType> {
  const data = await axios.get('./route-info.json');
  const vendorData: any = await axios.get('./vendor-data.json');

  const truckList: FtlTruckType[] =
    data?.data?.data?.reactStaticData?.trucks || [];
  const vendors: VendorMapType = getVendorMap(truckList);
  mergeVendorData(vendors, vendorData?.data || []);

  const eventList: FtlEventType[] =
    data?.data?.data?.reactStaticData?.events || [];
  const events: EventMapType = getEventMap(vendors, eventList);

  return {
    vendors,
    events,
  };
}
